import Controller from './controller/controller';
import View from './view/view';
import ModalControllers from './controller/modalControllers';
export default class App {
    constructor() {
        this.view = new View();
        this.controller = new Controller();
        this.modalControllers = new ModalControllers();
    }
    start() {
        this.initListeners();
        this.view.reloadPage(this.controller.reloadPage());
    }
    initListeners() {
        this.initHeaderListeners();
        this.initModalListeners();
        this.initProductPageListeners();
        this.initShoppingCartPageListener();
        this.initStorePageListeners();
        this.initRouterListener();
    }
    initStorePageListeners() {
        this.view.storePage.sideBar.filter.addEventListener('click', (event) => this.controller.clickFilter(event));
        this.view.storePage.products.addEventListener('click', (event) => this.controller.clickProductList(event));
        this.view.storePage.sorter.sorter.addEventListener('change', (event) => this.controller.sort(event));
        this.view.storePage.sideBar.sidebar.addEventListener('input', (event) => this.controller.sliderFilter(this.view.storePage.filterRange(event)));
        this.view.storePage.search.input.addEventListener('input', (event) => this.controller.search(event));
        this.view.storePage.sideBar.resetFilterButton.addEventListener('click', this.controller.resetFilter.bind(this.controller));
        this.view.storePage.view.view.addEventListener('click', this.controller.clickViewHandler.bind(this.controller));
    }
    initProductPageListeners() {
        this.view.productPage.buttonCart.addEventListener('click', (event) => this.controller.clickProductList(event));
        this.view.productPage.images.addEventListener('click', (event) => {
            this.controller.changeImages(event, this.view.productPage.mainImage);
        });
        this.view.productPage.buttonBuy.addEventListener('click', (event) => this.controller.openModalWindow(event, this.view.openModal.bind(this.view)));
    }
    initHeaderListeners() {
        this.view.header.logo.addEventListener('click', this.controller.openStartPage.bind(this.controller));
        this.view.header.shoppingCart.addEventListener('click', this.controller.openShoppingCart.bind(this.controller));
    }
    initShoppingCartPageListener() {
        this.view.shoppingCartPage.productItems.addEventListener('click', (event) => {
            this.controller.clickShoppingCartProduct(event);
        });
        this.view.shoppingCartPage.productsPage.numberPagesInput.addEventListener('input', (event) => {
            this.controller.shoppingInputPage(event);
        });
        this.view.shoppingCartPage.productsPage.switcher.addEventListener('click', (event) => {
            this.controller.changeShoppingPage(event);
        });
        this.view.shoppingCartPage.summary.buyButton.addEventListener('click', (event) => this.controller.openModalWindow(event, this.view.openModal.bind(this.view)));
    }
    initModalListeners() {
        this.view.modal.overlay.addEventListener('click', this.modalControllers.closeModal);
        this.view.modal.creditCard.creditCardNumber.addEventListener('input', (event) => {
            this.modalControllers.setPaymentSystem(event, this.view.modal.creditCard.paymentSystem);
        });
        this.view.modal.creditCard.expiration.addEventListener('input', (event) => {
            this.modalControllers.expirationSlash(event);
        });
        this.view.modal.creditCard.cvv.addEventListener('input', (event) => {
            this.modalControllers.enterCvv(event);
        });
        this.view.modal.modal.addEventListener('submit', (event) => {
            event.preventDefault();
            this.modalControllers.ordering(this.view.modal.modal, this.view.modal.creditCard.expiration, this.controller.openStartPage.bind(this.controller));
        });
    }
    initRouterListener() {
        window.addEventListener('popstate', (event, data = this.controller.reloadPage()) => this.view.reloadPage(data));
    }
}
